<template>
    <div class="ShoppingCart">
      <ShoppingTop :ShopTop="Shopping"></ShoppingTop>
<!--      <img src="../../assets/img/" alt="" class="title_bg">-->
      <div class="boxdiv CountSwiper" v-if="Onshow">
        <ul class="both pr">
          <li @click="cut(index)" v-for="(data,index) in PcbOnlineHome">{{data}}</li>
        </ul>
      </div>
      <div data-v-bc20844a="" class="Pcbhint" v-if="TextShow">
        请使用PC端，到捷配官网(www.jiepei.com)购物车中上传产品相关文件即可选择购买。
      </div>
      <router-view @getHome="HomeMsg" ref="child"></router-view>

    </div>
</template>

<script>
    import Vue from "vue"
    import ShoppingTop from "../common/ShoppingTop";
    import $ from "jquery";

    export default {
        name: "ShoppingCart",
        components: {ShoppingTop},
        data() {
            return {
              Shopping: { name: "我的购物车",left: true},
              PcbOnlineHome:["PCB/钢网","SMT(2)","薄膜开关面板","3D打印"],
              //判断是哪个页面
              HomeMsgHome:"",
              Onshow:true,
              TextShow:true,
              //判断是ocb钢网还是smt还是元器件还是博沫沫ianabn
              Allindex:"",
            }
        },
        methods: {
          cut(index){
            switch (index) {
              case 0:
                this.$router.push({path:"ShoppingHome"});
                break;
              case 1:
                this.$router.push({path:"ShoppingSMT"});
                break;
              case 2:
                this.$router.push({path:"ShoppingThinFlm"});
                break;
              default:
                this.$router.push({path:"ThinFilmPanel"});
                break;
            }
          },
          //父组件调用子组件的方法然后传递给另一个子组件
          FatherFunction(){
            this.$refs.child.callMethod()
          },
          HomeMsg(val){
            this.Allindex=val.index;
            //整个数据为空并且暂无订单为显示
            if(val.name=="0"&&val.show==true){
              this.Onshow=false;
              this.TextShow=false;
            }else if(val.name!="0"&&val.show==true){
              this.Onshow=true;
              this.TextShow=false;
            }else{
              this.TextShow=true;
              this.Onshow=true;
            }
            this.Allindex=val.index;
          },
        },
      mounted(){
        // 监听并添加蒙版
        let _this=this;
        let Onindex=_this.$data.Allindex;
        if(Onindex!=""){
          $($(".boxdiv li")[Onindex]).addClass("choose");
        }else{
          $($(".boxdiv li")[0]).addClass("choose");
        }
        $(document).ready(function() {
          $('.boxdiv').on('scroll', function(e) {
            if ($(this).scrollLeft() === 0) {
              $(this).find("ul").addClass("extrudeLeft");
              $(this).find("ul").removeClass("extrudeRight");
            }
            // console.log($(this).width(),$(this).scrollLeft(),$('.boxdiv ul').width());
            if (($(this).width() + $(this).scrollLeft()) === Math.ceil($('.boxdiv ul').width())) {
              $(this).find("ul").addClass("extrudeRight");
              $(this).find("ul").removeClass("extrudeLeft")
            } else {
              $(this).removeClass("extrudeLeft");
              $(this).removeClass("extrudeRight")
            }
          });
          //滚动动画
          $(".boxdiv li").click(function() {
            $(this).addClass("choose");
            $(this).siblings().removeClass("choose");
            //滚动动画
            if ($(this).index() == 3) {
              // $(this).scrollLeft()="192"
              console.log($(this).scrollLeft())
              $(".boxdiv").animate({
                scrollLeft: 207 //让body的scrollTop等于pos的top，就实现了滚动
              }, 1000);
            } else if ($(this).index() == 2) {
              $(".boxdiv").animate({
                scrollLeft: 0 //让body的scrollTop等于pos的top，就实现了滚动
              }, 1000);
            }
          })
        })
      },
      created() {


      }
    }

</script>

<style scoped>
  .title_bg{width: 100%}
  .boxdiv {overflow-x: scroll;height: 0.91rem; width: 100%; }
  .boxdiv ul {white-space: nowrap; display: inline-block;}
  .boxdiv ul li {font-size: 0.28rem;padding-left: 0.3rem;margin-right: 0.3rem;padding-right: 0.3rem; height: 0.91rem;line-height: 0.91rem; white-space: nowrap; display: inline-block; margin-right: -4px;position: relative;}
  .boxdiv ul li:last-child {margin-right: 0;}
  .boxdiv ul::-webkit-scrollbar {display: none;}
  .choose:after {content: "";display: table;clear: both;position: absolute;bottom: 0;width: 0.36rem;left: 50%;margin-left: -0.18rem;background: red;height: 0.08rem;}
  .CountSwiper .choose {font-size: 0.34rem;font-weight: bold;color: #333;}
  .CountSwiper {width: 100%;height: auto;position: relative;background: #fff;border-bottom: 1px solid #e3e4e8;}
  body ::-webkit-scrollbar {
    display: none;
  }
  .van-collapse-item .van-cell {
    border-bottom: 1px solid #ebedf0;
  } .van-cell::after {
      border-bottom: none;
    }
</style>



