<template>
    <div class="ShoppingTop bg_fff" >

      <div class="shade"></div>
      <div class="Head" :class="ShopTop.class==true?'bg_fff':''">
        <i class="iconfont pa left24" @click="prev"></i>
        {{ShopTop.name}}
        <div class="manage" @click="manages">管理</div>
      </div>
    </div>
</template>

<script>
    import Vue from "vue"

    export default {
        name: "ShoppingTop",
      props:["ShopTop"],
        components: {},
        data() {
            return {}
        },
        methods: {
          prev(){
            this.$router.go(-1)
          },
          //调用父组件的方法
          manages(){
            const fatherMethod = this.$parent.FatherFunction();
          },
        },
        created() {

        }
    }

</script>

<style scoped>
  .manage{    display: inline-block;position: absolute;right: .24rem;font-size: .28rem;}
  .ShoppingTop .Head{background: #fff}

</style>



